.button {
  min-width: 132px;
  min-height: 30px; 
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  border-radius: 5px;
}

.secondary {
  color:#0066CC;
  background-color: transparent;
  border: 1px solid #0066CC;
}

.default {
  background-color: #0066CC;
  color: white;
  border: none;
}

.red {
  background-color: #CC1439;
}

.disabled {
  background-color: #004D99;
  color: #002E5C;
  opacity: 50%;
}

.default:hover {
  background-color: #0080FF;
}

.red:hover {
  background-color:#FF1A47;
}

.secondary:hover {
  background-color: #E5F0FA;
}


.default:active {
  background-color: #0059B2;
}

.secondary:active {
  background-color: #C9E5FF;
}

.red:active {
  background-color: #B21232;
}

.button:disabled {
  background-color: #004D99;
  color: #002E5C;
  opacity: 50%;
}
